@import url(https://fonts.googleapis.com/css?family=ABeeZee:400,400i&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

html {
  background: linear-gradient(128.03deg, #BB6BD9 13.39%, #483A4E 78.11%);
}

body {
  height: 100vh;
  /* width: 100vw; */
  font-size: 12px;
}


.header{background-color:rgba(0,0,0,0.2);height:76px;display:flex;align-items:center;justify-content:space-between;padding:0 6em;font-family:'ABeeZee';font-size:12px}.header img{width:auto;height:38px}.header nav{align-self:center}.header nav ul{list-style:none}.header nav ul li{display:inline-block;text-transform:uppercase;margin-right:6em;color:white;letter-spacing:0.03em}.header nav ul li .active{color:#F2C94C}.header nav ul li a{text-decoration:none;color:white}.header nav ul li:last-child{margin-right:0}.header nav ul li:hover{cursor:pointer}

